import { Component, OnInit } from '@angular/core';
import { AkunExpensesSummaryService, AkunExpensesSummaryItemService, GaBiayaService, GaKlaimPengajuanService, AkunReimburseService, AkunCashAdvanceService } from 'src/app/sdk/hr/services';
import { AssetModelxService } from 'src/app/sdk/asset/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService, NzDrawerService } from 'ng-zorro-antd';
import { FormBuilderTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router, ActivatedRoute } from '@angular/router';
import { SingleUploadService } from 'src/app/components/document/single-upload.service';
import { Expenses_summary, Expenses_summary_item, Cash_advance, Biaya, Klaim_pengajuan, Reimburse } from 'src/app/sdk/hr/models';

@Component({
    selector: 'app-detail',
    templateUrl: './detail.component.html',
    styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {

    constructor(
        private akunExpensesSummaryService: AkunExpensesSummaryService,
        private akunExpensesSummaryItemService: AkunExpensesSummaryItemService,
        private gaBiayaService: GaBiayaService,
        private gaKlaimPengajuanService: GaKlaimPengajuanService,
        private akunReimburseService: AkunReimburseService,
        private akunCashAdvanceService: AkunCashAdvanceService,
        private assetModelxService: AssetModelxService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
        private drawerService: NzDrawerService,
        private singleUploadService: SingleUploadService,
    ) { }

    toogleUpload = false

    dataSum: Expenses_summary = {}
    list_item: Expenses_summary_item[] = []
    id_expenses_summary: string = ''

    dataCA: Cash_advance = {}
    dataBiaya: Biaya = {}
    dataKlaim: Klaim_pengajuan = {}
    dataReimburse: Reimburse = {}



    ngOnInit() {
        this.id_expenses_summary = this.activatedRoute.snapshot.paramMap.get('id')
        this.getData()
        this.getListItem()


    }

    getData() {
        this.spinner.show()
        this.akunExpensesSummaryService.getAkunExpensesSummariesId(this.id_expenses_summary).subscribe(
            data => {
                this.dataSum = data.data;
                this.getBiaya(data.data.ref_id_module);
                this.getCA(data.data.ref_id_module);
                this.getReimburse(data.data.ref_id_module);
                this.getKlaim(data.data.ref_id_module);
                this.spinner.hide()

            }
        )
    }

    getBiaya(ref_id_module) {
        this.gaBiayaService.getGaBiayasId(ref_id_module).subscribe(
            data => {
                this.dataBiaya = data.data;
            }
        )
    }

    getCA(ref_id_module) {
        this.akunCashAdvanceService.getAkunCashAdvancesId(ref_id_module).subscribe(
            data => {
                this.dataCA = data.data;
            }
        )
    }

    getReimburse(ref_id_module) {
        this.akunReimburseService.getAkunReimbursesId(ref_id_module).subscribe(
            data => {
                this.dataReimburse = data.data;
            }
        )
    }

    getKlaim(ref_id_module) {
        this.gaKlaimPengajuanService.getGaKlaimPengajuansId(ref_id_module).subscribe(
            data => {
                this.dataKlaim = data.data;
            }
        )
    }

    getListItem() {
        this.akunExpensesSummaryItemService.getAkunExpensesSummaryItems(JSON.stringify({ id_expenses_summary: this.id_expenses_summary })).subscribe(
            res => {
                this.list_item = res.data;
                this.spinner.hide()
            }
        )
    }



    goToBack() {
        this.router.navigate(['/ga/account/expenses-summary/list'])
    }

}
