import { Component, OnInit } from '@angular/core';
import { AkunExpensesSummaryService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzMessageService } from 'ng-zorro-antd';
import { PesanService } from 'src/app/services/tool/pesan.service';

@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {

    constructor(
        private akunExpensesSummaryService: AkunExpensesSummaryService,
        private spinner: NgxSpinnerService,
        private nzMessageService: NzMessageService,
        private pesan: PesanService,
    ) {}

    pageIndex = 1
    pageSize = 10
    total = 1
    listOfData: any[] = []
    loading = true
    sortValue: string | null = null
    sortKey: string | null = null
    search: string = ''

    ngOnInit() {
        this.searchData()
    }
    sort(sort: { key: string; value: string }): void {
        this.sortKey = sort.key
        this.sortValue = sort.value
        this.searchData()
    }

    searchData(reset: boolean = false): void {
        if (reset) {
            this.pageIndex = 1
        }
        this.loading = true
        this.akunExpensesSummaryService
            .getAkunExpensesSummaries(
                JSON.stringify({
                    pageIndex: this.pageIndex,
                    pageSize: this.pageSize,
                    sortKey: this.sortKey,
                    sortValue: this.sortValue,
                    search: this.search,
                }),
            )
            .subscribe((data: any) => {
                this.loading = false
                this.total = data.data.total
                this.listOfData = data.data.data
                console.log(data.data)
            })
    }

    updateFilter(value: string): void {
        this.search = value
        this.searchData(true)
    }

    delete(id) {
        this.spinner.show()
        this.akunExpensesSummaryService.deleteAkunExpensesSummariesId(id).subscribe(
            data => {
            this.searchData()
                },
            err => {
                this.pesan.pesanWarningForm('Data tidak boleh di delete')
                this.spinner.hide()
            },

        )
    }

    batalDelete() {
        this.nzMessageService.info('click cancel')
    }

}
