import { Component, OnInit } from '@angular/core';
import { AkunExpensesSummaryService, AkunExpensesSummaryItemService, GaBiayaService, GaKlaimPengajuanService, AkunReimburseService, AkunCashAdvanceService } from 'src/app/sdk/hr/services';
import { AssetModelxService } from 'src/app/sdk/asset/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService, NzDrawerService } from 'ng-zorro-antd';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Expenses_summary, Expenses_summary_item, Cash_advance, Biaya, Klaim_pengajuan, Reimburse } from 'src/app/sdk/hr/models';
import { Modelx } from 'src/app/sdk/asset/models';
import { FormControl } from '@angular/forms';
import { SearchItemComponent } from 'src/app/components/search-item/search-item/search-item.component';
import { SingleUploadService } from 'src/app/components/document/single-upload.service';

@Component({
    selector: 'app-edit',
    templateUrl: './edit.component.html',
    styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(
        private akunExpensesSummaryService: AkunExpensesSummaryService,
        private akunExpensesSummaryItemService: AkunExpensesSummaryItemService,
        private gaBiayaService: GaBiayaService,
        private gaKlaimPengajuanService: GaKlaimPengajuanService,
        private akunReimburseService: AkunReimburseService,
        private akunCashAdvanceService: AkunCashAdvanceService,
        private assetModelxService: AssetModelxService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
        private drawerService: NzDrawerService,
        private singleUploadService: SingleUploadService,
    ) { }

    toogleUpload = false

    dataSum: Expenses_summary = {}
    list_item: Expenses_summary_item[] = []
    id_expenses_summary: string = ''

    listCA: Cash_advance[] = []
    listBiaya: Biaya[] = []
    listKlaim: Klaim_pengajuan[] = []
    listReimburse: Reimburse[] = []
    listModel: Modelx[] = []

    ngOnInit() {
        this.id_expenses_summary = this.activatedRoute.snapshot.paramMap.get('id')
        this.form()
        this.getData()
        this.getListItem()

        this.singleUploadService.getCallback().subscribe(
            data => {

                if (data == 'akutansi_summary.finish') {
                    this.toogleUpload = false;
                }
            }
        )

        this.singleUploadService.getId().subscribe(
            data => {
                if (data) {
                    this.dataForm.patchValue({ id_dokumen: data })
                    this.simpan()
                }
            }
        )

    }
    togleShowUpload() {
        this.toogleUpload = true;
    }

    getPreeData() {

        this.listBiaya = []
        this.listCA = []
        this.listKlaim = []
        this.listReimburse = []
        if (this.dataForm.value.ref_module == 'Billing') {
            this.gaBiayaService.getGaBiayas().subscribe(data => {
                this.listBiaya = data.data
            })
        }
        if (this.dataForm.value.ref_module == 'Reimburse') {
            this.akunReimburseService.getAkunReimburses().subscribe(data => {
                this.listReimburse = data.data
            })
        }
        if (this.dataForm.value.ref_module == 'Ocupatient') {
            this.gaKlaimPengajuanService.getGaKlaimPengajuans().subscribe(data => {
                this.listKlaim = data.data
            })
        }
        if (this.dataForm.value.ref_module == 'Cash Advance') {
            this.akunCashAdvanceService.getAkunCashAdvances().subscribe(data => {
                this.listCA = data.data
            })
        }

    }


    getData() {
        this.akunExpensesSummaryService.getAkunExpensesSummariesId(this.id_expenses_summary).subscribe(
            data => {
                this.dataForm.setValue(data.data)

                this.getListItem()

                this.singleUploadService.param.next({
                    for_module: "akutansi_summary",
                    for_table: "erp_akutansi.expenses_summary",
                    for_module_id: data.data.id_expenses_summary
                })
            },
            err => {
                this.pesan.pesanWarningForm('Gagal di load data')
            },
            () => {
                console.log('HTTP request completed.')
            },
        )
    }


    dataForm: FormGroupTypeSafe<Expenses_summary>
    form() {
        this.dataForm = this.fb.group<Expenses_summary>({
            id_expenses_summary: new FormControl(''),
            // kantor_request: new FormControl(''),
            ref_id_module: new FormControl(''),
            // request_for: new FormControl(''),
            id_dokumen: new FormControl(''),
            ref_module: new FormControl(''),
            tanggal: new FormControl(''),
            created_at: new FormControl(''),
            updated_at: new FormControl(''),
            validasi: new FormControl(''),
            id_user: new FormControl(''),
        })
    }

    // ## LIST ITEM -----------------------------------
    id_model: string = null;
    add_item() {
        const drawerRef = this.drawerService.create<SearchItemComponent, { value: string }, string>({
            nzTitle: 'Search Model',
            nzContent: SearchItemComponent,
            nzWidth: '900px',
            nzContentParams: {
                value: 'arifan'
            }
        });

        drawerRef.afterOpen.subscribe(() => {
            console.log('Drawer(Component) open');
        });

        drawerRef.afterClose.subscribe(data => {

            if (typeof data === 'string') {
                this.id_model = data;
                this.beforeModel()
            }
        });
    }


    getListItem() {
        let param = {
            id_expenses_summary: this.id_expenses_summary
        }
        this.akunExpensesSummaryItemService.getAkunExpensesSummaryItems(JSON.stringify(param)).subscribe(
            data => {
                this.list_item = data.data
            }
        )
    }

    deleteItem(id) {
        this.spinner.show()
        this.akunExpensesSummaryItemService.deleteAkunExpensesSummaryItemsId(id).subscribe(
            data => {
                this.getListItem()
                this.spinner.hide()
            }
        )
    }


    dataItem: any = {};

    beforeModel() {
        console.log("panggie ko")
        this.assetModelxService.getAssetModelxesId(this.id_model).subscribe(
            data => {
                this.dataItem.keyword = data.data.keyword;
                this.dataItem.part_number = data.data.part_number;
                this.dataItem.id_model = data.data.id_model;
                this.dataItem.qty = 1;
                this.dataItem.harga_satuan = '';

                console.log(this.dataItem)
            }
        )
    }

    simpan_item() {
        if (this.dataItem.qty < 1) {
            alert('tidak boleh kosong');
            return false;
        }

        let param: Expenses_summary_item = {
            id_model: this.dataItem.id_model,
            qty: this.dataItem.qty,
            id_expenses_summary: this.id_expenses_summary,
            harga_satuan: this.dataItem.harga_satuan
        };

        this.spinner.show()
        //simpan ka server
        this.akunExpensesSummaryItemService.postAkunExpensesSummaryItems(param).subscribe(
            data => {
                this.id_model = null;
                this.spinner.hide();
                this.getListItem()
            },
            err => {
                this.spinner.hide();
                this.pesan.pesanWarningForm('Dupikat pembelian Item!')
            }
        )
    }

    // ## END  LIST ITEM -----------------------------------



    simpan() {
        this.spinner.show()

        if (this.list_item.length < 1) {
            this.pesan.pesanWarningForm('Anda harus pilih Item Pembelian!')
            this.spinner.hide()
            return
        }

        this.dataSum = this.dataForm.value
        this.akunExpensesSummaryService.putAkunExpensesSummariesId({ id: this.id_expenses_summary, body: this.dataSum }).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            },
            err => {
                this.pesan.pesanWarningForm('Gagal di simpan')
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            },
        )
    }

    goToBack() {
        this.router.navigate(['/ga/account/expenses-summary/list'])
    }

}
